#rc-loreal-shampoo {
  @tailwind base;
  @tailwind components;
  @tailwind utilities;
  @tailwind screens;
}

@layer base {
}

.rc-bg-hero {
  background: linear-gradient(158.74deg, rgba(37, 30, 22, 0.1972) 9.28%, rgba(37, 30, 22, 0) 44.34%),
    linear-gradient(228.29deg, rgba(37, 30, 22, 0.1972) -1.64%, rgba(37, 30, 22, 0) 47.33%), linear-gradient(0deg, #ffffff, #ffffff);
}

.rc-grow {
  transition: all 0.2s ease-in-out;
}

.rc-grow:hover {
  transform: scale(1.2) translateY(-10px);
}

.rc-active {
  opacity: 1;
  z-index: 1;
  transform: translateX(0);
}

.rc-inactive {
  opacity: 0;
  z-index: 0;
  transform: translateX(-100%);
}

.rc-col-outer {
  width: 35%;
}

.rc-col-inner {
  width: 30%;
}

.rc-select-none {
  user-select: none;
}

@media (min-width: 480px) {
  .rc-bg-hero {
    background: linear-gradient(222.69deg, rgba(37, 30, 22, 0.1972) 0.35%, rgba(37, 30, 22, 0) 42.36%),
      linear-gradient(294.94deg, rgba(37, 30, 22, 0.1972) 1.7%, rgba(37, 30, 22, 0) 38.2%),
      linear-gradient(0deg, #FFFFFF, #FFFFFF);
  }
}

@font-face {
  font-family: 'LorealPro-ExtraLight';
  src: url('./fonts/LOREAL_PRO-1-ExtraLight.otf');
}

@font-face {
  font-family: 'LorealPro-Book';
  src: url('./fonts/LOREAL_PRO-2-Book.otf');
}

@font-face {
  font-family: 'LorealPro-Medium';
  src: url('./fonts/LOREAL_PRO-3-Medium.otf');
}

@font-face {
  font-family: 'LorealPro-Semibold';
  src: url('./fonts/LOREAL_PRO-4-SemiBold.otf');
}

@font-face {
  font-family: 'LorealPro-Bold';
  src: url('./fonts/LOREAL_PRO-5-Bold.otf');
}
